<template>
  <v-container>
    <v-row class="text-center" v-for="(pelicula,index) in peliculas" :key="index">
      <v-col cols="12">
        <v-card color="#ffe91f">
          <v-card-title color="black" class="justify-center">
            <h2
              class="titulo-pelicula"
            >{{pelicula.title}} - Episode {{pelicula.episode_id}} ({{pelicula.release_date | fecha_formateada}})</h2>
          </v-card-title>


          <v-row class="my-2">
            <v-col cols="12" sm="6">
              <img
                :src="getPic(pelicula.episode_id)"
                alt="foto-pelicula"
                style="max-height: 400px; max-width:200px"
              />
            
            </v-col>

            <v-col>
              <v-card-subtitle>
                <h3>"{{pelicula.opening_crawl}}"</h3>
              </v-card-subtitle>

              <br />

              <v-card-text>
                <h3>Release Date:</h3>
                {{pelicula.release_date | fecha_formateada}}
              </v-card-text>

              <v-card-text>
                <h3>Director:</h3>
                {{pelicula.director}}
              </v-card-text>

              <v-card-text>
                <h3>Producer:</h3>
                {{pelicula.producer}}
              </v-card-text>
            </v-col>
          </v-row>

          <v-card-actions class="justify-space-around">
            <v-row>
              <v-col cols="12" sm="4">
                <v-btn
                  class="pa-5 yellow--text"
                  dark
                  :loading="loadingPersonajes"
                  @click.stop="personajesEpisodio(index)"
                  width="175"
                >Personajes</v-btn>
              </v-col>

              <v-col cols="12" sm="4">
                <v-btn
                  class="pa-5 green--text"
                  dark
                  :loading="loadingPlanetas"
                  @click.stop="planetasEpisodio(index)"
                  width="175"
                >Planetas</v-btn>
              </v-col>

              <v-col cols="12" sm="4">
                <v-btn
                  class="pa-5 blue--text"
                  dark
                  :loading="loadingRazas"
                  @click.stop="razasEpisodio(index)"
                  width="175"
                >Razas</v-btn>
              </v-col>

              <v-col cols="12" sm="4">
                <v-btn
                  class="pa-5 orange--text"
                  dark
                  :loading="loadingNaves"
                  @click.stop="navesEpisodio(index)"
                  width="175"
                >Naves</v-btn>
              </v-col>

              <v-col cols="12" sm="4">
                <v-btn
                  class="pa-5 pink--text"
                  dark
                  :loading="loadingVehiculos"
                  @click.stop="vehiculosEpisodio(index)"
                  width="175"
                >Vehiculos</v-btn>
              </v-col>
            </v-row>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>

    <v-dialog v-model="dialogPersonajes" max-width="290">
      <v-card class="mx-auto" max-width="400" dark>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title
              v-for="(personaje,index) in personajes"
              :key="index"
              style="color:#ffe91f"
            >{{personaje}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="#ffe91f" text @click="dialogPersonajes = false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogPlanetas" max-width="290">
      <v-card class="mx-auto" max-width="400" dark>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title
              v-for="(planeta,index) in planetas"
              :key="index"
              style="color:green"
            >{{planeta}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="green" text @click="dialogPlanetas = false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogRazas" max-width="290">
      <v-card class="mx-auto" max-width="400" dark>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title
              v-for="(raza,index) in razas"
              :key="index"
              style="color:rgb(51, 138, 238)"
            >{{raza}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="blue" text @click="dialogRazas = false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogNaves" max-width="290">
      <v-card class="mx-auto" max-width="400" dark>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title
              v-for="(nave,index) in naves"
              :key="index"
              style="color:orange"
            >{{nave}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="orange" text @click="dialogNaves = false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogVehiculos" max-width="290">
      <v-card class="mx-auto" max-width="400" dark>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title
              style="color: #D81B60"
              v-for="(vehiculo,index) in vehiculos"
              :key="index"
            >{{vehiculo}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn style="color: #D81B60" text @click="dialogVehiculos = false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>


<script>
import axios from "axios";
import store from "@/store";

export default {
  name: "Inicio",
  store,

  data() {
    return {
      dialogPersonajes: false,
      dialogPlanetas: false,
      dialogRazas: false,
      dialogNaves: false,
      dialogVehiculos: false,
      loadingPersonajes: false,
      loadingPlanetas: false,
      loadingRazas: false,
      loadingNaves: false,
      loadingVehiculos: false,
      imagenes: [
        "episodio1.jpg",
        "episodio2.jpg",
        "episodio3.jpg",
        "episodio4.jpg",
        "episodio5.jpg",
        "episodio6.jpg",
      ],
      personajes: [],
      planetas: [],
      razas: [],
      naves: [],
      vehiculos: [],
    };
  },
  computed: {
    peliculas() {
      return this.$store.state.peliculas;
    },
  },

  methods: {
    getPic(index) {
      return require("../assets/" + this.imagenes[index - 1]);
    },

    personajesEpisodio(episodio) {
      this.loadingPersonajes = true;
      let arreglo = this.$store.state.peliculas[episodio].characters;

      arreglo.forEach((peli, index) => {
        axios
          .get(peli)
          .then((res) => {
            this.personajes[index] = res.data.name;
          })
          .catch(function (error) {
            alert("Hubo un error en la peticion");
          });
      });

      setTimeout(() => {
        this.dialogPersonajes = true;
        this.loadingPersonajes = false;
      }, 3000);
    },

    planetasEpisodio(episodio) {
      this.loadingPlanetas = true;
      let arreglo = this.$store.state.peliculas[episodio].planets;

      arreglo.forEach((planet, index) => {
        axios
          .get(planet)
          .then((res) => {
            this.planetas[index] = res.data.name;
          })
          .catch(function (error) {
            alert("Hubo un error en la peticion");
          });
      });

      setTimeout(() => {
        this.dialogPlanetas = true;
        this.loadingPlanetas = false;
      }, 3000);
    },

    razasEpisodio(episodio) {
      this.loadingRazas = true;
      let arreglo = this.$store.state.peliculas[episodio].species;

      arreglo.forEach((raza, index) => {
        axios
          .get(raza)
          .then((res) => {
            this.razas[index] = res.data.name;
          })
          .catch(function (error) {
            alert("Hubo un error en la peticion");
          });
      });

      setTimeout(() => {
        this.dialogRazas = true;
        this.loadingRazas = false;
      }, 3000);
    },

    navesEpisodio(episodio) {
      this.loadingNaves = true;
      let arreglo = this.$store.state.peliculas[episodio].starships;

      arreglo.forEach((nave, index) => {
        axios
          .get(nave)
          .then((res) => {
            this.naves[index] = res.data.name;
          })
          .catch(function (error) {
            alert("Hubo un error en la peticion");
          });
      });

      setTimeout(() => {
        this.dialogNaves = true;
        this.loadingNaves = false;
      }, 3000);
    },

    vehiculosEpisodio(episodio) {
      this.loadingVehiculos = true;
      let arreglo = this.$store.state.peliculas[episodio].vehicles;

      arreglo.forEach((vehiculo, index) => {
        axios
          .get(vehiculo)
          .then((res) => {
            this.vehiculos[index] = res.data.name;
          })
          .catch(function (error) {
            alert("Hubo un error en la peticion");
          });
      });

      setTimeout(() => {
        this.dialogVehiculos = true;
        this.loadingVehiculos = false;
      }, 3000);
    },
  },
};
</script>
<style>
@media (max-width: 600px) {
  .titulo-pelicula {
    font-size: 20px !important;
  }
}

.titulo-pelicula {
  color: black;
  text-shadow: 1px 1px 2px black;
  font-size: 40px;
}
</style>

