<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <v-card class="mx-auto" max-width="600">
          <v-img class="white--text align-end" height="400px" src="@/assets/personajes.jpg">
            <v-card-title>
              <h2 class="text-center ma-3 pa-3 heading-4 titulos">Personajes de Star Wars</h2>
            </v-card-title>
          </v-img>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="ma-3">
      <v-col cols="12" sm="6" v-for="personaje in personajes" :key="personaje.name">
        <v-card class="mx-auto text-center" max-width="400" color="#ffe91f">
          <v-card-text>
            <div>
              <strong>Birth Year:</strong>
              {{personaje.birth_year}}
            </div>
            <p
              class="display-1 text--primary font-weight-black"
              style="text-shadow:1px 1px 2px black;"
            >{{personaje.name}}</p>
            <p>
              <strong>Gender:</strong>
              {{personaje.gender}}
            </p>
            <div class="text--primary">
              <strong>Eye Color:</strong>
              {{personaje.eye_color}}
              <br />
              <strong>Hair Color:</strong>
              {{personaje.hair_color}}
              <br />
              <strong>Height:</strong>
              {{personaje.height}} cm.
              <br />
              <strong>Weight:</strong>
              {{personaje.mass}} kg.
              <br />
              <strong>Skin Color:</strong>
              {{personaje.skin_color}}
              <br />
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-pagination
      circle
      color="#555555"
      :length="9"
      v-model="pagina"
      @input="traerPersonajes()"
      @next="traerPersonajes()"
      @previous="traerPersonajes()"
    ></v-pagination>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "Personajes",
  data() {
    return {
      personajes: [],
      pagina: 1,
      cantPaginas: 1,
    };
  },
  methods: {
    traerPersonajes() {
      const params = {
        page: this.pagina,
      };
      axios
        .get("https://swapi.dev/api/people/", { params })
        .then((res) => {
          this.personajes = res.data.results;
          this.cantPaginas = res.data.count / 10 + 1;
        })
        .catch(function (error) {
          alert("Hubo un error en la peticion");
        });
    },
  },
  created() {
    this.traerPersonajes();
  },
};
</script>

<style>
.titulos {
  color: #ffe91f;
  text-shadow: 3px 3px 5px white;
  font-size: 40px;
}
</style>