<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <v-card max-width="1200" class="mx-auto">
          <v-app-bar dark color="#ffe91f">
            <v-toolbar-title>Razas de Star Wars</v-toolbar-title>
          </v-app-bar>

          <v-container>
            <v-row dense>
              <v-col cols="12" v-for="(raza, index) in razas" :key="raza.name">
                <v-card :color="colores[index]" dark>
                  <v-card-title class="headline titulo-raza">{{raza.name}}</v-card-title>

                  <v-card-subtitle>
                    <strong>Classification:</strong>
                    {{raza.classification}}
                    <br />
                    <strong>Designation:</strong>
                    {{raza.designation}}
                    -
                    <strong>Language:</strong>
                    {{raza.language}}
                    -
                    <strong>Lifespan:</strong>
                    {{raza.average_lifespan}}
                  </v-card-subtitle>

                  <v-card-text>
                    <strong>Average Height:</strong>
                    {{raza.average_height}}
                    <br />
                    <strong>Skin Colors:</strong>
                    {{raza.skin_colors}}
                    <br />
                    <strong>Hair Colors:</strong>
                    {{raza.hair_colors}}
                    <br />
                    <strong>Eye Colors:</strong>
                    {{raza.eye_colors}}
                    <br />
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-col>
    </v-row>

    <v-pagination
      circle
      color="#555555"
      :length="4"
      v-model="pagina"
      @input="traerRazas()"
      @next="traerRazas()"
      @previous="traerRazas()"
    ></v-pagination>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "Razas",
  data() {
    return {
      razas: [],
      pagina: 1,
      cantPaginas: 1,
      colores: [
        "#333333",
        "#9e975b",
        "#333333",
        "#9e975b",
        "#333333",
        "#9e975b",
        "#333333",
        "#9e975b",
        "#333333",
        "#9e975b",
        "#333333",
      ],
    };
  },
  methods: {
    traerRazas() {
      const params = {
        page: this.pagina,
      };
      axios
        .get("https://swapi.dev/api/species/", { params })
        .then((res) => {
          this.razas = res.data.results;
          this.cantPaginas = res.data.count / 10 + 1;
        })
        .catch(function (error) {
          alert("Hubo un error en la peticion");
        });
    },
  },
  created() {
    this.traerRazas();
  },
};
</script>

<style>
@media (max-width: 330px) {
  .v-toolbar__title {
    font-size: 20px !important;
  }
}
.v-toolbar__title {
  color: black;
  font-weight: bold;
  font-size: 30px;
  text-shadow: 1px 1px 2px black;
}

.titulo-raza {
  text-shadow: 1px 1px 2px black;
}
</style>