<template>
  <v-container>
    <h2 class="text-center ma-3 pa-3 heading-4 titulos">Naves de Star Wars</h2>

    <v-row>
      <v-col cols="12" md="6" v-for="nave in naves" :key="nave.name">
        <v-card class="mx-auto" max-width="450" color="#ffe91f" outlined>
          <v-img src="@/assets/nave.jpg" height="200px"></v-img>

          <v-card-title class="titulo-nave justify-center">{{nave.name}}</v-card-title>

          <v-card-subtitle>
            <br />
            <strong>Model:</strong>
            {{nave.model}}
            <br />
            <strong>Class:</strong>
            {{nave.starship_class}}
          </v-card-subtitle>

          <v-card-subtitle>
            <strong>Manufacturer:</strong>
            {{nave.manufacturer}}
          </v-card-subtitle>

          <v-card-actions>
            <v-btn icon block @click="show = !show" class="titulo-nave">
              Ms Info
              <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="show">
              <v-divider></v-divider>

              <v-card-text class="text-center">
                <strong>Length:</strong>
                {{nave.length}}
                <br />
                <strong>Crew:</strong>
                {{nave.crew}}
                <br />
                <strong>Passengers:</strong>
                {{nave.passengers}}
                <br />
                <strong>Cargo capacity:</strong>
                {{nave.cargo_capacity}}
                <br />
                <strong>Cost in credits:</strong>
                {{nave.cost_in_credits}}
                <br />
                <strong>Max Speed:</strong>
                {{nave.max_atmosphering_speed}}
                <br />
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </v-col>
    </v-row>

    <v-pagination
      circle
      color="#555555"
      :length="4"
      v-model="pagina"
      @input="traerNaves()"
      @next="traerNaves()"
      @previous="traerNaves()"
    ></v-pagination>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "Naves",
  data() {
    return {
      naves: [],
      pagina: 1,
      cantPaginas: 1,
      show: false,
    };
  },
  methods: {
    traerNaves() {
      const params = {
        page: this.pagina,
      };
      axios
        .get("https://swapi.dev/api/starships/", { params })
        .then((res) => {
          this.naves = res.data.results;
          this.cantPaginas = res.data.count / 10 + 1;
        })
        .catch(function (error) {
          alert("Hubo un error en la peticion");
        });
    },
  },
  created() {
    this.traerNaves();
  },
};
</script>

<style>
.titulo-nave {
  text-shadow: 1px 1px 2px black;
}
</style>